<template>
<div class="col-12" v-if="ready_display">
    <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
    <div class="card">
        <div class="card-header">
                <strong>View Procurement Form - {{ pid }} Category - {{ category }}</strong>
        </div>
    <div class="card-body card-block" style='padding:0 0 0 0'>
    <div v-if="step==1"> <!--Step 1-->
        <!-- info -->
        <div class='inner_card_bd'>
            <label><b>1.1: Procurement Info.</b></label><br/><br/>
            <div class="row form-group">
                <div class="col-5"><label class="form-control-label">Committee / Department</label></div>  <!--cause fresh-->
                <div class="col-7">
                    <input type='text' class='form-control' v-model="dept" readonly/>
                </div>
            </div> 
            <div class="row form-group">
                <div class="col-5"><label class="form-control-label">Asset Category</label></div>  <!--cause fresh-->
                <div class="col-7">
                    <input type='text' class='form-control' v-model="asset_cat" readonly/>
                </div>
            </div>  
            <!--
            <div class="row form-group">
                <div class="col-5"><label class="form-control-label">Procurement Display Name</label></div>  
                <div class="col-7">
                    <input type="text" class="form-control" v-model="dp_name" required readonly/>
                </div>
            </div> 
            -->
        </div>
        <div class='inner_card_bd' style='background-color:rgb(229,229,229)'>
            <label><b>1.2: Company Info.</b></label><br/><br/>  
            <div class="row form-group">
                <div class='col-12' style='display:flex;flex-direction:row'>
                    <div class='col-12' style='padding-left:0;padding-right:0'>
                        <div style='display:flex;flex-direction:row'>
                            <label style='padding-top:0.5em;margin-right:0.5em'>1.</label>
                            <input type="text" class="form-control" placeholder="Enter Company Name" v-model="company_name[0]" readonly/>
                        </div>                    
                    </div>
                </div>           
            </div>
            <div class="row form-group" v-for="n in counter_company">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-12' style='padding-left:0;padding-right:0'>
                            <div style='display:flex;flex-direction:row'>
                                <label style='padding-top:0.5em;margin-right:0.5em'>{{ n+1 }}.</label>
                                <input type="text" class="form-control" placeholder="Enter Company Name" v-model="company_name[n]" readonly/>
                            </div>                    
                        </div>
                    </div>           
            </div>
        </div>
            <div class='inner_card_bd'>
                <label><b>1.3: {{ doc_type }} Info.</b></label><br/><br/>  
                <div class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0;'>
                            <label>Title</label>
                            <input type="text" class="form-control" placeholder="Enter Header of Tender/Written Quotation (e.g. Supply of ... )" v-model="dp_name" readonly/>                       
                        </div>
                        <div class='col-5'>
                            <label>Fund Source(s)</label>
                            <input type="text" class="form-control" placeholder="Enter Funding Source(s)" v-model="fsrc" readonly/>
                        </div>
                        <div class='col-2'>
                            <label>Site Visit</label>
                            <input type='text' class='form-control' v-model="site" readonly/>
                        </div>
                    </div>           
                </div>
                <div v-if="site=='yes'" class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <label>Site Visit Info</label>
                            <input type="date" class="form-control" v-model="v_date" readonly/>
                        </div>                     
                        <div class='col-5'>
                            <label>&nbsp;</label>
                            <input type="time" class="form-control" v-model="v_time" readonly/>
                        </div>
                    </div>           
                </div>
                <div class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <label>Contact Person</label>
                            <input type="text" class="form-control" placeholder="Search Contact Person" v-model="cp_name" required @keyup="search_user" readonly/>
                        </div>                     
                        <div class='col-5'>
                            <label>Contact Number</label>
                            <input type="text" class="form-control" placeholder="Enter Contact Number" v-model="cp_num" required readonly/>
                        </div>
                    </div>           
                </div>
                <div class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <label>Letter Open Date</label>
                            <input type="date" class="form-control" v-model="op" readonly/>
                        </div>                     
                        <div class='col-5'>
                            <label>Return Deadline</label>
                            <input type="date" class="form-control" v-model="close" readonly/>
                        </div>
                        <div class='col-2'>
                            <label>Type</label>
                            <input type='text' class='form-control' v-model="ssb" readonly/>
                        </div>
                    </div>           
                </div>
                <div class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <label>Item Name</label>
                            <input type="text" class="form-control" placeholder="Enter Item Description (Header of Tender)" v-model="item_name[0]" readonly/>
                        </div>                     
                        <div class='col-5'>
                            <label>Quantity</label>
                            <div style='display:flex;flex-direction:row'>
                                <input type="number" class="form-control col-7" v-model="quantity[0]" required @change="update_progress(100)" readonly/>
                                <input type='text' class='form-control col-5' v-model="unit[0]" placeholder='Enter Unit' required readonly/>
                            </div>
                        </div>
                    </div>           
                </div>
                <div class="row form-group" v-for="n in counter_item">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <input type="text" class="form-control" placeholder="Enter Item Description" v-model="item_name[n]" readonly/>
                        </div>                     
                        <div class='col-5'>
                             <div style='display:flex;flex-direction:row'>
                                <input type="number" class="form-control col-7" v-model="quantity[n]" required @change="update_progress(100)" readonly/>
                                <input type='text' class='form-control col-5' v-model="unit[n]" placeholder='Enter Unit' required readonly/>
                            </div>
                        </div>
                    </div>           
                </div>
            </div>
        <div class='inner_card_bd' style='background-color:rgb(229, 229, 229)' v-if="isTWQ">
        <div class="row form-group">
                <div class="col col-md-12">
                    <div style="display:flex;flex-direction:row">
                    <div v-if="signed==false">
                        <div style="display:flex;flex-direction:row">
                            <label class="form-control-label" style="margin-right:1em;padding-top:0.4em">    
                                Signature Security Code : 
                            </label>
                                <input type="password" v-model='pwd' class="form-control" style="width:10em;margin-right:1em"/>                   
                                <button type="button" class="form-control" ref="submitcode" @click="post_ssc" style="width:10em">Sign now !</button>         
                        </div>   
                    </div>
                    <div v-if="signed" style="display:flex;flex-direction:row;">
                        <label class="form-control-label" style="margin-right:1em;padding-top:0.4em;margin-top:2.5em">    
                            Signature 
                        </label>
                            <img :src="sign_data[0].signature" style="width:auto;height:5em;border-bottom:1px solid black;margin-left:0.5em"/>        
                    </div>        
                    </div>
                </div>
            </div>
            <div v-html="message">{{message}}</div>
        </div>         
    </div>
    <!--step 2-->
    <div v-if="(step==2)&&(ready_step2)"> 
        <label><b>Step 2: Fill In Quotation Info.</b></label><br/><br/>  
        <!--item-->
        <div class="table-responsive">    
                <table v-for="i in counter_company+1" style='width:100%'  class="table table-striped first-td-padding">
                    <tr style='width:100%'>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em;width:40%' @click="change_child_display(i)">
                            {{ company_name[i-1] }}      
                        </th>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em' @click="change_child_display(i)">
                            Unit Cost
                        </th>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em' @click="change_child_display(i)">
                            Total Cost     
                        </th>                    
                    </tr>
                    <tbody :id="'display'+(i-1)" style="display:'';width:100%" >
                    <tr v-for="j in counter_item+1" >               
                        <td style='font-size:1.5em'>{{ item_name[j-1] }}</td>
                        <td>
                            <input type='number' step="0.001" class='form-control' placeholder="Enter Unit Cost"  @change="cal_sum((i-1),(j-1))" v-model="cost_item[(i-1)][(j-1)]"/>                        
                        </td>
                        <td>
                            <input type='number' step="0.001" class='form-control' placeholder="Enter Unit Cost" readonly :id="'cost_total['+(i-1)+']['+(j-1)+']'" v-model="cost_total[(i-1)][(j-1)]"/>                        
                        </td>                
                    </tr>
                    </tbody>
                </table>                                  
        </div>
    </div>

    <!--escape step-->
    </div>
        <div class="card-footer">
            <button type="button" class="btn btn-primary btn-nt" @click.prevent="next_step" v-if="(step<2)&&(this.cat_lv<3)">
              <i class="fa fa-paper-plane"></i> Next Step
            </button>
            <button type="submit" class="btn btn-primary btn-sm" v-if="(step==2)||(this.cat_lv>2)" :disabled="processing">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="previous_step">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>

        </form>
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.id v-on:click.prevent="select_sup(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div>
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="user_list"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_user"
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.id v-on:click.prevent="select_u(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div>
</div>

</template>


<script>
import axios from 'axios';
export default {
    data(){          
        return{
            processing:false,
            unit:[],
            ssb:'',
            doc_type:'',
            signed:false,
            pwd:'',
            sign_data:[],
            message:'',
            isTWQ:false,
            dp_name:'',
            user_list:[],
            hv_user:false,
            v_date:'',
            v_time:'',
            cp_name:'',
            cp_num:'',
            site:'no',
            counter_old:0,
            pid:'',
            fsrc:'',
            ten_list:[],
            op:'',
            close:'',
            tendor:'',
            ready_display:false,
            sup_list:[],
            user_check:false,
            cat_list:['5001 - 50,000','50,001 - 200,000','>200,000'],
            category:'5001 - 50,000',
            counter_company:1,
            counter_item:0,
            cat_lv:2,
            company_name:[],
            company_address:[],
            company_contact:[],
            cost_total:[],
            cost_item:[],
            list_item:[],
            tmp:0,
            count_total:0,
            item_name:[],
            item:[],
            ready:false,
            dept_list:[],
            display_child:[true],
            quantity:[],
            step:1,
            pdf:[], 
            searching:0,    
            recep_data:[],       
            check_company:false,
            check_item:false,
            check_quant:false,
            hv_record:false,
            asset_list:[],
            asset_cat:'',
            new_co:[],
            ready_step2:false,
            fetch_counter:[],
            fetch_comp:[],
            fetch_item:[],
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            ], 
        }  
    },
async created(){
    await axios.get('Procurement/procurement/backend/TWQA_permission.php')
        .then(
            (response)=>{
                if(response.data=='YES'){
                    this.isTWQ=true;
                } else if(response.data=='NO'){
                    this.isTWQ=false;
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].department;
            }
        ); 
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<1){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    var formData = new FormData();
    formData.append('dept',this.dept);
    await axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.asset_list = response.data;
                    this.asset_cat = this.asset_list[0].name;
                }
            )
    var formData_fetch = new FormData();

    var proc_id = document.getElementById('tmp_id').value;
    this.pid = proc_id;
    formData_fetch.append('proc_id',proc_id);
    await axios.post('Procurement/procurement/backend/fetch_edit_proc_counter.php',formData_fetch)
            .then(
                (response)=>{
                    this.fetch_counter = response.data;
                    this.counter_company = this.fetch_counter.counter_company;
                    this.counter_old = this.counter_company;
                    this.counter_item = this.fetch_counter.counter_item;
                }
            )
    await axios.post('Procurement/procurement/backend/fetch_edit_proc_company.php',formData_fetch)
        .then(
            (response)=>{
                this.fetch_comp = response.data;
                this.dp_name = this.fetch_comp[0].record_descript;
                this.dept = this.fetch_comp[0].dept_full;
                this.asset_cat = this.fetch_comp[0].asset_full;
                this.cat_lv = Number(this.fetch_comp[0].category);
                if(this.cat_lv==2){                   
                    this.category = '5001 - 50,000';
                } else if(this.cat_lv==3){
                    this.doc_type = 'Written Quotation';
                    this.category = '50,001 - 200,000';
                } else if(this.cat_lv==4){
                    this.doc_type = 'Tender';
                    this.category = '>200,000';
                }
                var i = 0;
                for(i=0;i<this.counter_company+1;i++){
                    this.company_name[i] = this.fetch_comp[i].company_name;
                }
                var j = 0;
                for(j=0;j<this.counter_item+1;j++){
                    var item_num = Number(this.counter_company+1) * (j+1) - 1;
                    this.item_name[j] = this.fetch_comp[item_num].item_name;
                    this.quantity[j] = this.fetch_comp[item_num].quantity;
                    this.unit[j] = this.fetch_comp[item_num].item_unit;
                }
            }
        )
        await axios.post('Procurement/procurement/backend/fetch_proc_td.php',formData_fetch)
            .then(
                (response)=>{
                    this.ten_list = response.data;
                    this.tendor = this.ten_list[0].tendor_title;
                    this.ssb = this.ten_list[0].ssb;
                    if(this.ssb =='yes'){
                        this.ssb='SSB';
                    } else {
                        this.ssb = 'Regular';
                    }
                    this.op = this.ten_list[0].date1;
                    this.close = this.ten_list[0].date2;
                    this.fsrc = this.ten_list[0].fundsrc;
                    this.cp_name = this.ten_list[0].cont_p;
                    this.cp_num = this.ten_list[0].cont_num;
                    this.v_date = this.ten_list[0].v_date;
                    if(this.v_date.length>0){
                        this.site='yes';
                    }
                    this.v_time =this.ten_list[0].v_time;
                }
            )
        this.ready_display = true;
  },
  
async mounted(){
    //asset category of department   

},   
    methods:{
            post_ssc(event){
            var formData = new FormData();
            formData.append('pwd',this.pwd);
            axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
            .then(
                (response)=>{
                    this.sign_data = response.data;
                    if(response.data[0].signature==undefined){
                        this.message = response.data;
                    } else {
                    this.signed=true;
                    this.message='';
                    }
                }
            )
        },
        select_u(name){
            this.cp_name = name;
            this.hv_user = false;
        },
        search_user(){
            var key = this.cp_name;
            if(key==''){
                this.hv_user=false;
            } else {
                var formData = new FormData();
                formData.append('key',key);
                axios.post('Procurement/procurement/backend/search_user.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='Empty')){
                                this.hv_user = true;
                                this.user_list = response.data;
                            } else {
                                this.user_list = '';
                                this.hv_user = false;
                            }
                        }
                    )
            }
        },
        load_info(){

        },
        addfile_pdf(event){
        var n = event;
             this.pdf[n] = document.getElementById(n).files;         
        }, 
        add_company(){
            this.counter_company = this.counter_company+1;
        },
        remove_company(){
            this.counter_company = this.counter_company-1;
        },
        add_item(){
            this.counter_item = this.counter_item+1;
        },
        remove_item(){
            this.counter_item = this.counter_item-1;
        },
        async next_step(){
            var i = 0;
            var j = 0;
            var x = 0;
            var y = 0;
            var n=0;
            this.check_company = false;
            this.check_item = false;
            for(i=0;i<this.counter_company+1;i++){
                  if(this.new_co[i]==true){
                    if((this.company_name[i]==undefined)||(this.company_address[i]==undefined)||(this.company_address[i]==undefined)){
                        this.check_company = false;
                    } else if((!(this.company_name[i].length>0)||!(this.company_address[i].length>0)||!(this.company_address[i].length>0))){
                        this.check_company = false;
                    } else if((this.company_name[i].length>0)&&(this.company_address[i].length>0)&&(this.company_address[i].length>0)){
                        this.check_company = true;
                    }
                } else {
                    if(this.company_name[i]==undefined){
                        this.check_company = false;
                    } else if((this.step==1)&&!(this.company_name[i].length>0)){
                        this.check_company = false;
                    } else if((this.step==1)&&(this.company_name[i].length>0)){
                        this.check_company = true;
                    }  
                }
            };  
            for(j=0;j<this.counter_item+1;j++){
                    if((this.item_name[j]==undefined)||(this.quantity[j]==undefined)){
                    this.check_item = false;
                } else if((this.step==1)&&!(this.item_name[j].length>0)&&!(this.quantity[j]>0)){
                    this.check_item = false;
                } else if((this.step==1)&&(this.item_name[j].length>0)&&(this.quantity[j]>0)){
                    this.check_item = true;
                }    
            }  
            if((this.check_company==true)&&(this.check_item==true)){
                for(x=0;x<this.counter_company+1;x++){
                    this.cost_total[x] = [];
                    this.cost_item[x] = [];
                    for(y=0;y<this.counter_item+1;y++){
                        this.cost_total[x][y] = 0;
                        this.cost_item[x][y] = 0;
                    }
                }
                this.step = this.step+1;
            } else if((this.check_company==false)){
                this.$alert('Please Enter Company Info.')
            } else if((this.check_item==false)){
                this.$alert('Please Enter Item Info.');
            }
            var formData_fetch = new FormData();
            var proc_id = document.getElementById('tmp_id').value;
            formData_fetch.append('proc_id',proc_id);
            await axios.post('Procurement/procurement/backend/fetch_edit_proc_item_cost.php',formData_fetch)
            .then(
                (response)=>{
                    this.fetch_item = response.data;
                    var k = 0;
                    var l = 0;
                    var n = 0;
                    var c = 0;
                    if(this.counter_old>this.counter_company){
                        c = this.counter_company;
                    } else {
                        c = this.counter_old;
                    }
                    for(k=0;k<(c+1);k++){
                        for(l=0;l<this.counter_item+1;l++){
                            this.cost_item[k][l] = Number(this.fetch_item[n].unit_cost);
                            this.cost_total[k][l] = Number(this.fetch_item[n].quantity) * this.cost_item[k][l];
                            n++;
                        }
                    } 
                }
            )
            this.ready_step2 = true;
        },
        previous_step(){
            if(this.step==2){
            this.step = this.step-1;
            } else {
                this.$router.go(-1);
            };
        },
        change_child_display(id){
        var display_id = 'display'+(id-1);
            if(document.getElementById(display_id).style.display!='none'){
                document.getElementById(display_id).style.display='none';
            } else if(document.getElementById(display_id).style.display=='none'){
                document.getElementById(display_id).style.display='';
            }
        },
        cal_sum(x,y){   
            var id = 'cost_total['+x+']['+y+']';
            this.cost_item[x][y] = Number(this.cost_item[x][y]);            
            var tmp = Number(this.cost_item[x][y]) * Number(this.quantity[y]);      
            this.cost_total[x][y] = Number(tmp);
            document.getElementById(id).value=this.cost_total[x][y]
        },
        update_cat(event){
            if(this.category=='5001 - 50,000'){
                this.cat_lv=2;
                this.counter_company=1;
            } else if(this.category=='50,001 - 200,000'){
                this.cat_lv=3;
                this.counter_company=4;
            } else if(this.category=='50,001 - >200,000'){
                this.cat_lv=4;
            }
        },
        search_company(event){
            this.searching = event;
            var formData = new FormData();
            formData.append('key',this.company_name[event]);
            axios.post('Procurement/shared_backend/supplier_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record=false; 
                        this.new_co[event]=false;
                    } else if(response.data == 'new'){
                        this.new_co[event]=true;
                        this.hv_record=false;
                    } else {
                        this.new_co[event]=false;
                        this.recep_data = response.data;
                        this.hv_record=true;
                    }
                }); 
        }, 
        select_sup(event){
            var id = this.searching;
            this.company_name[id] = event;
            this.hv_record=false;
        },
        async handleSubmit(){
            var i = 0;
            var j = 0;
            var p = 0;
            var bi = 0;
            var formData = new FormData();
            var count_files = 0;
            var proc_id = document.getElementById('tmp_id').value;
            formData.append('op',this.op);
            formData.append('close',this.close);
            formData.append('ssb',this.ssb);
            formData.append('display',this.dp_name);
            formData.append('date1',this.op);
            formData.append('date2',this.close);
            formData.append('title',this.tendor);
            formData.append('fund_src',this.fsrc);
            formData.append('proc_id',proc_id);
            formData.append('dept',this.dept);
            formData.append('asset',this.asset_cat);
            formData.append('category',this.cat_lv);
            formData.append('v_date',this.v_date);
            formData.append('v_time',this.v_time);
            formData.append('cp_name',this.cp_name);
            formData.append('cp_num',this.cp_num);
            formData.append('counter_company',this.counter_company);
            formData.append('counter_item',this.counter_item);
            for(bi=0;bi<this.counter_item+1;bi++){
                formData.append('item'+bi,this.item_name[bi]);
                formData.append('quantity'+bi,this.quantity[bi]);
            }
            for(i=0;i<this.counter_company+1;i++){
                count_files = 0;
                formData.append('company_'+i,this.company_name[i]);  
                if(this.new_co[i]==true){
                    formData.append('new_sup_'+i,this.new_co[i]);
                    formData.append('company_address_'+i,this.company_address[i]); 
                    formData.append('company_contact_'+i,this.company_contact[i]); 
                }              
                for(j=0;j<this.counter_item+1;j++){
                    formData.append('com_'+i+'_item_'+j,this.item_name[j]);   
                    formData.append('item_unit_'+j,this.unit[j]);
                    //formData.append('com_'+i+'_ucost_'+j,this.cost_item[i][j]);    
                    formData.append('com_'+i+'_ucost_'+j,0);                  
                }
                if(this.pdf[i]!=undefined){
                    for(p=0;p<this.pdf[i].length;p++){
                        let file = this.pdf[i][p];
                        formData.append('files'+i+'_'+p, file);  
                        count_files = count_files+1;
                    }
                }
                formData.append('count_file'+i, count_files);
                
            }
            if(this.signed==true){
                this.processing = true;
                formData.append('sign_code',this.sign_data[0].signature);
                await axios.post('Procurement/procurement/backend/edit_new_proc_sign.php',formData)
                .then(
                    (response)=>{
                        this.$alert('Successfully Submitted !').then(
                            ()=>{
                                this.processing = false;                               
                            }
                        )    
                        this.$router.push('view_proc');              
                    }
                );
            } else {
                this.$alert('Please Sign Before Submit !');
            }
        }
    }
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


